"use client";

import { useState } from 'react';
import { content } from '@/data/content';
import styles from './ProgramGallery.module.css';
import { X, CheckCircle2 } from 'lucide-react';

export default function ProgramGallery() {
  const { offer } = content;
  const [selectedItem, setSelectedItem] = useState<null | typeof offer.programSteps[0]>(null);

  // Fallback if programSteps is not defined yet (during refactor transition)
  const steps = offer.programSteps || [];

  return (
    <section className={styles.section} id="program">
      <div style={{ textAlign: 'center', marginBottom: '4rem' }}>
        <h2 style={{ fontSize: '2.5rem', fontWeight: 800, color: '#fff' }}>Learn With Andi's Program</h2>
      </div>

      <div className={styles.grid}>
        {steps.map((step, index) => (
          <div
            key={index}
            className={styles.card}
            onClick={() => setSelectedItem(step)}
            role="button"
            tabIndex={0}
          >
            <span className={styles.stepNumber}>{step.number}</span>
            <div className={styles.cardContent}>
              <h3 className={styles.title}>{step.title}</h3>
              <p className={styles.subtitle}>{step.subtitle}</p>
            </div>
          </div>
        ))}
      </div>

      {/* Modal Overlay */}
      {selectedItem && (
        <div className={styles.modalOverlay} onClick={() => setSelectedItem(null)}>
          <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
            <button
              className={styles.closeButton}
              onClick={() => setSelectedItem(null)}
              aria-label="Close modal"
            >
              <X size={24} color="#fff" />
            </button>

            <span style={{ fontSize: '4rem', fontWeight: 800, color: 'rgba(255,215,0,0.1)', display: 'block', marginBottom: '-2rem' }}>
              {selectedItem.number}
            </span>
            <h3 className={styles.modalTitle}>{selectedItem.title}</h3>
            <p className={styles.modalSubtitle}>{selectedItem.subtitle}</p>
            <p className={styles.modalDescription}>{selectedItem.description}</p>

            <ul className={styles.modalPoints}>
              {selectedItem.points?.map((point, idx) => (
                <li key={idx} className={styles.pointItem}>
                  <CheckCircle2 color="#FFD700" size={20} className={styles.pointIcon} />
                  <span>{point}</span>
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
    </section>
  );
}
